"use client";

import { useEffect, useState } from "react";

const CHAINS = ["HyperEVM", "SuiMVM", "Ethereum", "Arbitrum"];

export default function Hero() {
  const [activeChain, setActiveChain] = useState(0);
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
    const interval = setInterval(() => {
      setActiveChain((prev) => (prev + 1) % CHAINS.length);
    }, 2800);
    return () => clearInterval(interval);
  }, []);

  const stagger = (delay: number) => ({
    opacity: mounted ? 1 : 0,
    transform: mounted ? "translateY(0)" : "translateY(16px)",
    transitionDelay: `${delay}s`,
    transitionDuration: "0.8s",
    transitionTimingFunction: "cubic-bezier(.4, .4, 0, 1)",
  });

  return (
    <section className="relative min-h-screen flex flex-col items-center justify-center px-6 pt-28 pb-24">
      <div className="relative z-10 max-w-3xl mx-auto text-center">
        {/* Small tag */}
        <p
          className="text-[11px] text-[#999] tracking-[0.15em] uppercase mb-8 font-mono"
          style={stagger(0.1)}
        >
          Multi-chain lending protocol
        </p>

        {/* Heading */}
        <h1
          className="text-3xl sm:text-5xl md:text-6xl font-light tracking-tight leading-[1.15] mb-8 text-[#1a1a1a]"
          style={stagger(0.25)}
        >
          Lend on one chain.
          <br />
          <span className="font-normal">Borrow on another.</span>
        </h1>

        {/* Divider */}
        <div
          className="divider w-12 mx-auto mb-8"
          style={stagger(0.35)}
        />

        {/* Sub */}
        <p
          className="text-sm md:text-base text-[#999] max-w-md mx-auto mb-14 leading-[1.8]"
          style={stagger(0.45)}
        >
          Supply assets on{" "}
          <span
            key={activeChain}
            className="inline-block text-[#333] font-medium"
            style={{ animation: "fadeSwap 0.4s ease-out" }}
          >
            {CHAINS[activeChain]}
          </span>
          {" "}and borrow on any other chain — all from your SUI wallet.
        </p>

        {/* CTAs */}
        <div
          className="flex flex-col sm:flex-row items-center justify-center gap-5 mb-20"
          style={stagger(0.55)}
        >
          <a
            href="#start"
            className="px-8 py-3 text-sm text-[#f5f3ee] bg-[#333] rounded-none hover:bg-[#1a1a1a]"
          >
            Start Lending
          </a>
          <a
            href="#howitworks"
            className="text-sm text-[#999] border-b border-[#ccc] pb-0.5 hover:text-[#333] hover:border-[#333]"
          >
            Learn more
          </a>
        </div>

        {/* Minimal flow: source → protocol → target */}
        <div className="flex items-center justify-center gap-6 md:gap-10" style={stagger(0.7)}>
          <div className="text-center">
            <p className="text-xs font-mono text-[#4a6fa5] mb-1">HEVM</p>
            <p className="text-[11px] text-[#bbb]">Lend</p>
          </div>

          <div className="flex items-center gap-3">
            <span className="w-8 h-px bg-[#ddd]" />
            <span className="text-[10px] text-[#ccc] font-mono">IKA</span>
            <span className="w-8 h-px bg-[#ddd]" />
          </div>

          <div className="text-center">
            <p className="text-xs font-mono text-[#333] font-medium mb-1">Manbow</p>
            <p className="text-[11px] text-[#bbb]">Protocol</p>
          </div>

          <div className="flex items-center gap-3">
            <span className="w-8 h-px bg-[#ddd]" />
            <span className="text-[10px] text-[#ccc] font-mono">IKA</span>
            <span className="w-8 h-px bg-[#ddd]" />
          </div>

          <div className="text-center">
            <p className="text-xs font-mono text-[#4a6fa5] mb-1">MVM</p>
            <p className="text-[11px] text-[#bbb]">Borrow</p>
          </div>
        </div>
      </div>

      {/* Scroll hint */}
      <div className="absolute bottom-10 left-1/2 -translate-x-1/2 flex flex-col items-center gap-2">
        <span className="w-px h-6 bg-[#ddd]" />
        <span className="text-[9px] font-mono text-[#ccc] tracking-[0.2em]">SCROLL</span>
      </div>
    </section>
  );
}
